import * as React from "react";

import styled from "react-emotion";
import Section from "./Section";
import memberData from "../lib/members.json";

const Category = ({
  header,
  members
}: {
  header: string;
  members: string[];
}) => (
  <div>
    <CategoryHeader>{header}</CategoryHeader>
    <ul>
      {members.map(member => (
        <li>
          <a href={members.members[member]}>{member}</a>
        </li>
      ))}
    </ul>
  </div>
);

const Members = ({ className }: { className?: string }) => {
  const categories = Object.entries(members.categories).sort(
    (a, b) => (a < b ? -1 : 1)
  );
  return (
    <Section className={className}>
      <h2>Members</h2>
      {categories.map(([header, catMembers]) => (
        <Category header={header} members={catMembers} />
      ))}
    </Section>
  );
};

export default styled(Members)``;
